@import "https://unpkg.com/open-props/easings.min.css";

/* Reset some basic styles */
:root {
  /* --background-color: #f4f4f4; */
  --text-color: #333;
  --banner-bg: linear-gradient(135deg, #ff7e5f, #feb47b);
  --banner-text-color: white;
  --dashboard-bg: white;
  --input-bg: #fff;
  --input-border-color: #ccc;
  --button-bg: #28a745;
  --button-hover-bg: #218838;
}

/* Dark Theme */
[data-theme='dark'] {
  --background-color: #121212;
  --text-color: #e0e0e0;
  --banner-bg: linear-gradient(135deg, #4f1250, #074063);
  --banner-text-color: #f4f4f4;
  --dashboard-bg: #333;
  --input-bg: #444;
  --input-border-color: #666;
  --button-bg: #444;
  --button-hover-bg: #666;
}

body, h1, p {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  color: var(--text-color);
  /* background-color: var(--background-color); */
  box-sizing: border-box;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap');

/* Overall Page Layout */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
  background-color: var(--background-color);
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Banner Styling */
.banner {
  background: var(--banner-bg);
  color: var(--banner-text-color);
  padding: 40px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  width: 100%;
  max-width: 1000px;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  position: relative;
  overflow: hidden;
}

.banner-content {
  position: relative;
  z-index: 1;
}

.banner-title {
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 10px;
}

.timer {
  font-size: 1.5rem;
  font-weight: 300;
  margin-bottom: 20px;
}

.banner-link {
  background-color: rgba(255, 255, 255, 0.2);
  color: var(--banner-text-color);
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  font-weight: bold;
  transition: background-color 0.3s ease;
}

.banner-link:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.banner::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255,255,255,0.3) 10%, transparent 10%);
  background-size: 50px 50px;
  animation: rotate 30s linear infinite;
  z-index: 0;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.banner.hidden {
  opacity: 0;
  transform: translateY(-20px);
  pointer-events: none;
}

/* Dashboard Styling */
.dashboard {
  background-color: var(--dashboard-bg);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.dashboard label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: var(--text-color);
}

.dashboard input[type="text"],
.dashboard input[type="number"],
.dashboard input[type="checkbox"] {
  width: calc(100% - 20px);
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid var(--input-border-color);
  border-radius: 4px;
  background-color: var(--input-bg);
  color: var(--text-color);
  font-size: 1rem;
  transition: background-color 0.3s ease, border-color 0.3s ease, color 0.3s ease;
}

.dashboard input[type="checkbox"] {
  width: auto;
  margin-right: 20px;
  transform: translateX(5px)translateY(2px);
}

.dashboard button {
  background-color: var(--button-bg);
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  width: 100%;
  transition: background-color 0.3s ease;
}

.dashboard button:hover {
  background-color: var(--button-hover-bg);
}

/* Responsive Design */
@media (max-width: 768px) {
  .banner, .dashboard {
    width: 90%;
    padding: 15px;
  }
}

@media (max-width: 480px) {
  .banner-title {
    font-size: 2rem;
  }

  .timer {
    font-size: 1.2rem;
  }

  .dashboard input[type="text"],
  .dashboard input[type="number"] {
    font-size: 0.9rem;
    padding: 8px;
  }

  .dashboard button {
    padding: 8px 16px;
    font-size: 0.9rem;
  }
}
